import { Injectable, OnDestroy } from '@angular/core';
import { BroadcastService } from '@azure/msal-angular';
import { MsalService } from '@azure/msal-angular';
//import { environment } from 'src/environments/environment';
//import { UserDetailsClass } from '../model/UserDetails/UserDetailsClass';
//import { ClientsStore } from '../store/clients.store';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class MsalAuthService implements OnDestroy {

  public isLoggedIn: boolean = false;
  public token: string = '';
  // public userInfo: UserDetailsClass = {
  //   UsersPK: environment.defaultId,
  //   RolesPK: environment.defaultId,
  //   ClientsPK: environment.defaultId,
  //   RoleName: '',
  //   ObjectId: '',
  //   DisplayName: '',
  //   Email: '',
  //   Mobile: ''
  // };

  constructor(private broadcastService: BroadcastService, private authService: MsalService ,
               private router: Router) { }
  bindAuthEvents() {
      this.broadcastService.subscribe('msal:loginFailure', (payload) => {
          console.log('login failure' + JSON.stringify(payload));
          this.isLoggedIn = false;
          this.token = '';
          let authError = payload._errorDesc;
        
          if (authError == null) {authError = ""; }
         
          if (payload._error.indexOf('AADB2C90077') > -1) {
            this.logout();
            this.login();
        }

      });
      this.broadcastService.subscribe('msal:loginSuccess', (payload) => {
          this.isLoggedIn = true;

          if (this.isResetPassword()) {
              console.log('after password reset : ' + JSON.stringify(payload));
             
              this.logout();
          } else {
              console.log('login success' + JSON.stringify(payload));
              this.token = payload._token;
              this.getUserDetails();
          }

      });
      this.broadcastService.subscribe('msal:acquireTokenSuccess', (payload) => {
        console.log('GOT TOKEN' + JSON.stringify(payload));
   });

      this.broadcastService.subscribe('msal:acquireTokenFailure', (payload) => {
    console.log('TOKEN FAILED' + JSON.stringify(payload));
   });
  }

  isResetPassword() {
      return document.referrer.toLowerCase().indexOf('b2c_1a_passwordreset') > -1;
  }

  getUserDetails() {
    // // User Details
    if (this.getUser() == null) {return ; }

    const decodedToken = this.getUser().idToken;
    console.log(decodedToken);
   
 }
  isAuthenticated(): boolean {
    if (this.isLoggedIn) {
      if (this.authService.getUser()) {  return true; }
    }
    return false;
  }
  getUser() {
      if (this.isAuthenticated()) {
          return this.authService.getUser();
      }
      return null;
  }

 
  logout() {
    this.isLoggedIn = false;
  
    this.authService.logout();
  }


  login() {
     this.authService.loginRedirect();
  }

  forgotPassword() {
      const storage = this.authService.getCacheStorage();
      const authError: string = storage.getItem('msal.login.error') ? storage.getItem('msal.login.error') : null;
      if (authError && authError.indexOf('AADB2C90118') > -1) {
          return true;
      }

      return false;
  }



  ngOnDestroy() {
      this.broadcastService.getMSALSubject().next(1);
  }

}

